// @mui
import { Container, Typography, Card, Box, Stack, Alert } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { CodeBlock, EndpointCard } from '../../sections/docs';
import { HOST_API } from '../../config';

// ----------------------------------------------------------------------

export default function DocsWebhook() {
  const { themeStretch } = useSettings();
  const { setting, user } = useAuth();

  const webhookExample = `{
    "status": "success",
    "transaction_type": "data",
    "reference": "habukhan-dev-1234567890",
    "amount": "250.00",
    "phone": "08011111111",
    "network": "MTN",
    "plan": "1GB Monthly",
    "balance_before": "5000.00",
    "balance_after": "4750.00",
    "timestamp": "${new Date().toISOString()}"
}`;

  return (
    <Page title="Webhook Configuration">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Webhook Configuration"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Documentation', href: '/documentation/home' },
            { name: 'Webhook' },
          ]}
        />

        {/* Overview */}
        <Card sx={{ p: 4, mb: 3 }}>
          <Typography variant="h4" gutterBottom>
            Webhook Configuration
          </Typography>
          <Typography variant="body1" color="text.secondary">
            Configure webhooks to receive real-time notifications about transaction status updates. Webhooks are
            sent as POST requests to your specified URL.
          </Typography>
        </Card>

        {/* Setup */}
        <Card sx={{ p: 4, mb: 3 }}>
          <Typography variant="h5" gutterBottom>
            Setup Instructions
          </Typography>
          <Stack spacing={2}>
            <Box>
              <Typography variant="subtitle1" gutterBottom>
                1. Configure Webhook URL
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Go to your dashboard settings and add your webhook URL. This URL should be publicly accessible
                and accept POST requests.
              </Typography>
            </Box>
            <Box>
              <Typography variant="subtitle1" gutterBottom>
                2. Verify SSL Certificate
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Ensure your webhook URL uses HTTPS with a valid SSL certificate for security.
              </Typography>
            </Box>
            <Box>
              <Typography variant="subtitle1" gutterBottom>
                3. Handle POST Requests
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Your endpoint should accept JSON POST requests and return a 200 OK status.
              </Typography>
            </Box>
          </Stack>
        </Card>

        {/* Payload Example */}
        <EndpointCard
          method="POST"
          endpoint="https://your-domain.com/webhook"
          description="Webhook payload sent to your configured URL"
        >
          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Example Webhook Payload
            </Typography>
            <CodeBlock code={webhookExample} language="json" title="JSON Payload" />
          </Box>
        </EndpointCard>

        {/* Important Notes */}
        <Card sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Important Notes
          </Typography>
          <Stack spacing={2}>
            <Alert severity="warning">
              <Typography variant="body2">
                <strong>Response Time:</strong> Your webhook endpoint must respond within 5 seconds
              </Typography>
            </Alert>
            <Alert severity="info">
              <Typography variant="body2">
                <strong>Retry Logic:</strong> Failed webhooks are retried up to 3 times with exponential
                backoff
              </Typography>
            </Alert>
            <Alert severity="success">
              <Typography variant="body2">
                <strong>Security:</strong> Verify webhook authenticity using the signature header
              </Typography>
            </Alert>
          </Stack>
        </Card>
      </Container>
    </Page>
  );
}
